*{
    margin: 0 auto;
}
.main::-webkit-scrollbar{
    width: 10px;
}
.main::-webkit-scrollbar-track {
    background-color: #f1f1f1;
}

/* Scrollbar Handle */
.main::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 50px; /* Rounded corners for the scrollbar handle */
}